 */

/* -------- VARS -------- */

/* -------- STYLES -------- */

.dropzone-container {
  border-radius: 0;
  overflow: hidden;
}

.dropzone-container .circles-icons {
  cursor: pointer;
  pointer-events: none;
}

.dropzone-container .circle {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 200px;
  background: none;
  border: 1px solid #c4c4c4;
  pointer-events: none;
}

.dropzone-container .circle.circle-1 {
  width: 100px;
  height: 100px;
  margin: -50px 0 0 -50px;
}

.dropzone-container .circle.circle-2 {
  width: 148px;
  height: 148px;
  border-color: #d1d1d1;
  margin: -74px 0 0 -74px;
}

.dropzone-container .circle.circle-3 {
  width: 192px;
  height: 192px;
  border-color: #dedede;
  margin: -96px 0 0 -96px;
}

.dropzone-container .circle.circle-4 {
  width: 268px;
  height: 268px;
  border-color: #eaeaea;
  margin: -134px 0 0 -134px;
}

.dropzone-container .circle.circle-5 {
  width: 386px;
  height: 386px;
  border-color: #f7f7f7;
  margin: -193px 0 0 -193px;
}

.dropzone-container .uk-icon-rz-file {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 28px;
  height: 28px;
  margin: -14px 0 0 -14px;
  text-align: center;
  font-size: 28px;
  line-height: 28px;
}

.dropzone-container .dz-default.dz-message {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  background: none;
}

.dropzone-container .dz-default.dz-message span {
  position: absolute;
  display: block;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  border-top: 1px dashed #D2D2D2;
  text-align: center;
  font-size: 13px;
  line-height: 50px;
  background: #ECECEC;
  color: var(--global-text-lighter-color);
  letter-spacing: 0.05em;
  text-shadow: 0 0 4px #fff;
  z-index: 2;
}

.dropzone-container .dz-preview,
.dropzone-previews .dz-preview {
  margin: 0 12px 12px 0;
  padding: 0;
  border: none;
  box-shadow: none;
  font-size: 13px;
  min-width: 120px;
  min-height: 120px;
  background-color: rgba(0, 0, 0, 0.1);
}

.dropzone-container .dz-preview .dz-progress,
.dropzone-previews .dz-preview .dz-progress {
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
}

.dropzone-container .dz-preview .dz-progress .dz-upload,
.dropzone-previews .dz-preview .dz-progress .dz-upload {
  background-color: var(--rz-accent-color);
}

.dropzone-container .dz-preview .dz-details,
.dropzone-previews .dz-preview .dz-details {
  width: 80px;
  height: 80px;
  margin: 0;
  padding: 0;
  background: none;
}

.dropzone-container .dz-preview .dz-details img,
.dropzone-previews .dz-preview .dz-details img {
  width: 80px;
  height: 80px;
}

.dropzone-container .dz-preview.dz-image-preview:hover .dz-details img,
.dropzone-previews .dz-preview.dz-image-preview:hover .dz-details img {
  opacity: 1;
}

.dropzone-container .dz-preview .dz-success-mark,
.dropzone-previews .dz-preview .dz-success-mark,
.dropzone-container .dz-preview .dz-error-mark,
.dropzone-previews .dz-preview .dz-error-mark {
  z-index: 510;
}

.dropzone-container .dz-preview .dz-success-mark svg,
.dropzone-previews .dz-preview .dz-success-mark svg,
.dropzone-container .dz-preview .dz-error-mark svg,
.dropzone-previews .dz-preview .dz-error-mark svg {
  display: none;
}

.dropzone-container .dz-preview .dz-error-mark .uk-icon-rz-plus-simple,
.dropzone-previews .dz-preview .dz-error-mark .uk-icon-rz-plus-simple {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.dropzone-container .dz-preview .dz-error-message,
.dropzone-previews .dz-preview .dz-error-message {
  top: 0;
  padding: 5px;
  font-size: 13px;
  text-align: center;
  min-width: 0;
  max-width: none;
  left: 0;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
}

.dropzone-container .dz-preview .dz-error-message span,
.dropzone-previews .dz-preview .dz-error-message span {
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.dropzone-container .dz-preview.dz-error .dz-error-message,
.dropzone-previews .dz-preview.dz-error .dz-error-message {
  opacity: 1;
}

.dropzone-container .dz-preview.dz-error .dz-details,
.dropzone-previews .dz-preview.dz-error .dz-details {
  width: 150px;
}

.dz-details,
.dz-filename,
.dz-size {
  display: none;
}

.dz-image img {
  width: 120px;
  height: auto;
}

.dropzone-container.dz-started .dz-message {
  display: block;
  opacity: 1;
}

.dropzone-container.dz-started .dz-message .circles-icons {
  display: none;
}

