    GEOTAG WIDGET
\*------------------------------------*/

/* -------- VARS -------- */

/* -------- STYLES -------- */

.geotag-widget {
  position: relative;
}

.geotag-label {
  position: absolute;
  left: 66px;
  height: 55px;
  line-height: 59px;
  font-size: 13px;
  color: #555;
}

.geotag-widget-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 8px 0;
  position: relative;
  border: 1px solid #cbcbcb;
  border-bottom: 1px dashed #cbcbcb;
}

@media (min-width: 768px) {
  .geotag-widget-nav {
    flex-wrap: nowrap;
    padding: 0;
  }
}

.geotag-widget-nav__title {
  padding: 0 10px 0 10px;
  color: var(--global-title-color);
}

.geotag-widget-nav__title.label {
  font-size: 13px;
  padding-left: 0px;
  margin-left: 0;
}

.geotag-widget-nav .uk-icon-rz-map-marker,
.geotag-widget-nav .uk-icon-rz-map-multi-marker {
  font-size: 28px;
}

.geotag-widget-nav .geotag-widget-quick-creation {
  margin: 8px 0;
}

.geotag-widget-nav .uk-navbar-content {
  padding: 0 10px;
}

.geotag-widget-nav__head {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0 8px;
}

.geotag-widget-nav__content {
  padding: 0 8px;
}

@media (min-width: 768px) {
  .geotag-widget-nav__content {
    margin-left: auto;
  }
}

.rz-geotag-meta input[type="text"] {
  width: 280px;
}

.rz-geotag-canvas {
  border: 1px solid #cbcbcb;
  border-top: none;
  box-sizing: border-box;
}

.multi-geotag-group {
  position: relative;
  display: grid;
  height: 400px;
  grid-template-columns: 40% 60%;
}

@media (min-width: 768px) {
  .multi-geotag-group {
    grid-template-columns: 30% 70%;
  }
}

@media (min-width: 1450px) {
  .multi-geotag-group {
    grid-template-columns: 20% 80%;
  }
}

.multi-geotag-group .rz-geotag-canvas {
  box-sizing: border-box;
}

.multi-geotag-list-markers {
  display: block;
  overflow: hidden;
  overflow-y: auto;
  margin: 0;
  box-sizing: border-box;
  border-left: 1px solid #cbcbcb;
  border-bottom: 1px solid #cbcbcb;
  padding: 0;
}

.multi-geotag-list-markers li {
  list-style: none;
  display: block;
  border-bottom: 1px solid #e4e4e4;
  font-size: 13px;
  padding: 5px 8px;
}

.multi-geotag-list-markers li .button {
  float: right;
  display: block;
  width: 20px;
  height: 18px;
  color: #989898;
}

.multi-geotag-list-markers li .button:hover {
  color: #4b4b4b;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 500;
  margin-left: -20px;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  background-color: #333;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1s infinite ease-in-out;
  animation: sk-scaleout 1s infinite ease-in-out;
}

.spinner.light {
  background-color: var(--contrasted-bg);
}

@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}

/* VueJS Entities drawers, explorers */

/*------------------------------------*\
    EXPLORER
\*------------------------------------*/

/* -------- STYLES -------- */

:root {
  --explorer-width: 100vw;
}

@media (min-width: 768px) {
  :root {
    --explorer-width: 420px;
  }
}

@media (min-width: 1280px) {
  :root {
    --explorer-width: 430px;
  }
}

@media (min-width: 1450px) {
  :root {
    --explorer-width: 510px;
  }
}

@media (min-width: 1920px) {
  :root {
    --explorer-width: 610px;
  }
}

.widget-explorer {
  background-color: #3D3D3D;
  position: fixed;
  left: 0px;
  top: 0px;
  bottom: 0px;
  width: var(--explorer-width);
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 19000;
}

.widget-explorer a {
  outline: none;
}

.widget-explorer.explorer-open .widget-explorer-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
