  transition: opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.19, 1, 0.22, 1);
  transition: transform 0.5s cubic-bezier(0.19, 1, 0.22, 1), opacity 0.5s;
  transition: transform 0.5s cubic-bezier(0.19, 1, 0.22, 1), opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.slide-left-leave-to,
