/*
 * Styles for documents pages
 */


/* -------- STYLES -------- */


// List

table.documents {
    .platform-icon {

        i {
            font-size: 35px;
        }
    }
}

.documents-bulk-actions {
    display: none;

    &.visible {
        display: block;
    }
}

/*
 * Bulk linking for NODES and DOCUMENTS
 */
.node-bulk-tag-form,
.document-bulk-folder-form {
    display: inline-block;

    .data-uk-margin {
        display: inline-block;

        .uk-form-row {
            margin-top: 0;
        }

        .form-col-text {

        }
    }
}
@media screen and (min-width: 1281px) {
    .node-bulk-tag-form,
    .document-bulk-folder-form {
        .data-uk-margin {
            .form-col-text {
                width: 400px;
            }
        }
    }
}
@media screen and (min-width: 1380px) {
    .node-bulk-tag-form,
    .document-bulk-folder-form {
        .data-uk-margin {
            .form-col-text {
                width: 500px;
            }
        }
    }
}

.documents-list {
    margin:0;
    padding: 0 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 128px);
    grid-gap: 20px 15px;
    justify-content: center;

    @media screen and (min-width: @screen-sm-min) {
        padding: 0 50px;
    }

    .document-checkbox {
        top: 8px;
        right: 5px;
    }

    .document-mime-type {
        position: absolute;
        transform: translateY(-100%);
        top: 2px;
        height: 15px;
        font-size: 9px;
        line-height: 14px;
        font-weight: bold;
        -webkit-font-smoothing: antialiased;
        font-family: arial, helvetica, sans-serif;
        text-transform: uppercase;
        padding: 0 5px;
        color: white;
        background-color: var(--rz-accent-color);
        transition: 0.4s transform @easeOutExpo;
        z-index: 2;
    }

    .document-links {
        position: absolute;
        width: 128px;
        height: 30px;
        transform: translateY(100%);
        bottom: 0;
        overflow: hidden;
        transition: 0.4s transform @easeOutExpo;
        .box-sizing();

        .document-link {
            display: inline-block;
            width:64px;
            height:30px;
            border:none;
            border-radius: 0;
            .box-sizing();

            i {
                margin:0;
                line-height:30px;
                vertical-align: top;
            }

            .label {
                display: none;
            }
        }
    }

    .document-item {
        position: relative;
        display: block;
        vertical-align: top;
        width: 128px;
        margin: 0 auto;

        &:hover {
            .document-links{
                transform: translateY(0);
            }
            .document-mime-type{
                transform: translateY(0);
            }
            .document-platform-icon {
                opacity: 0;
            }
            .document-checkbox {
                display: block;
            }
        }
    }

    .document-overflow {
        position: absolute;
        top: 0;
        left: 0;
        width: 128px;
        height: 128px;
        overflow: hidden;
    }

    .document-border {
        position: absolute;
        top:0;
        right:0;
        left:0;
        height:4px;
        background: var(--rz-accent-color);
        transition: 0.5s background-color @easeOutExpo;
        z-index:3;
    }

    .document-image {
        width:128px;
        height:128px;
        background: #E2E2E2; // not white to see white PNG logotypes
        overflow: hidden; // SVG images are not cropped
    }

    .document-name{
        color: #909090;
        font-size: 10px;
        .text-ellipsis();
        margin-top: 2px;
        font-weight: bold;
    }

    .document-platform-icon{
        width: 35px; height: 35px;
        background-color: #FDFDFD;
        margin: -23px;
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 3px;
        text-align: center;
        line-height: 37px;
        .box-shadow(0px 0px 3px rgba(0, 0, 0, 0.1));
        opacity: 0.95;
        transition: 0.9s opacity @easeOutExpo;
        padding: 5px;

        i{
            font-size: 28px;
            line-height:37px;
            color: #3C3C3C;
        }
    }
}

// Edit preview
.content-document-preview{
    padding-bottom: 0;
    text-align: center;

    .uk-thumbnail{
        height:auto;
        background: none;
        box-shadow: none;

        iframe {
            max-width: 100%;
            border: 0;
        }
    }

    .document-source{
        margin-top:20px;
        font-size: @default-font-size;

        code{
            padding:5px;
            background: #b7b7b7;
            color:#fff;
            border:0 none;
        }
    }
}

.content-document-edit {
    .document-paths {

        dt, dd {
            font-size: @default-font-size;
            color: #555555;
        }

        dt {
            &:before {
              content: " ";
              color: #bbbbbb;
            }
        }
        dd {
            margin: 5px 0px 20px 0px;
            font-weight: lighter;
            letter-spacing: 0.03em;
        }
    }
}

#document_edit_embed {
    padding: 1.2em 2em;
    background-color: #f1f1f1;
}
